import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CRow,
  CFormLabel,
  CCardFooter,
  CModal,
  CModalHeader,
  CModalFooter,
  CModalTitle,
  CModalBody,
} from "@coreui/react-pro";
import React, { useEffect, useState } from "react";
import NavBar from "../../navbars/ev_super_admin/nav_normal_school";
import {
  faTimesCircle,
  faCheckCircle,
  faArrowRight,
  faSave,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Formik, Form, Field, FieldArray, ErrorMessage } from "formik";
import * as Yup from "yup";
import Loader from "src/loader/Loader";
import EVFormikControl from "src/ev_formik_controls/EVFormikControl";
import EDU_AXIOS from "src/helpers/edu_axios";
import notify from "src/helpers/notify";
import { Link } from "react-router-dom";
import { useHistory } from "react-router-dom";

const general_constraints = (props) => {
  let school_id = props.match.params.school_id;
  let [visible, setVisible] = useState(false);
  let [dataLoaded, setDataLoaded] = useState(false);
  let [dbData, setDbData] = useState({});
  let history = useHistory();
  // open cancle modal box
  let openModalBox = () => {
    setTimeout(() => setVisible(true), 0);
  };

  // close cancle modal box
  let closeModalBox = () => {
    setTimeout(() => setVisible(false), 0);
  };
  // options array
  let opt = [
    { text: "Yes", value: "Yes" },
    { text: "No", value: "No" },
    { text: "Don't know", value: "NA" },
  ];

  // days
  let days = [
    "Monday",
    "Tuesday",
    "Wednesday",
    "Thursday",
    "Friday",
    "Saturday",
    "Sunday",
    "NA",
  ];
  // formik params
  let initialValues;
  if (dbData) {
    initialValues = {
      cons_1: dbData.cons_1 ? dbData.cons_1 : "",
      cons_2: dbData.cons_2 ? dbData.cons_2 : "",
      cons_3: dbData.cons_3 ? dbData.cons_3 : "",
      cons_4: dbData.cons_4 ? dbData.cons_4 : "",
      cons_5: dbData.cons_5 ? dbData.cons_5 : "",
      cons_6: dbData.cons_6 ? dbData.cons_6 : "",
      cons_7: dbData.cons_7 ? dbData.cons_7 : "",
      cons_8: dbData.cons_8 ? dbData.cons_8 : "",
      cons_9: dbData.cons_9 ? dbData.cons_9 : "",
      cons_10: dbData.cons_10 ? dbData.cons_10 : "",
      cons_11: dbData.cons_11 ? dbData.cons_11 : "",
      cons_12: dbData.cons_12 ? dbData.cons_12 : "",
      cons_13: dbData.cons_13 ? dbData.cons_13 : "",
      cons_14: dbData.cons_14 ? dbData.cons_14 : "",
      cons_15: dbData.cons_15 ? dbData.cons_15 : "",
      cons_16: dbData.cons_16 ? dbData.cons_16 : "",
      cons_17: dbData.cons_17 ? dbData.cons_17 : "",
    };
  } else {
    initialValues = {
      cons_1: "",
      cons_2: "",
      cons_3: "",
      cons_4: "",
      cons_5: "",
      cons_6: "",
      cons_7: "",
      cons_8: "",
      cons_9: "",
      cons_10: "",
      cons_11: "",
      cons_12: "",
      cons_13: "",
      cons_14: "",
      cons_15: "",
      cons_16: "",
      cons_17: "",
    };
  }

  // validation rules of the form fields
  const validationSchema = Yup.object().shape({
    cons_1: Yup.string().required("required"),
    cons_2: Yup.string().required("required"),
    cons_3: Yup.string().required("required"),
    cons_4: Yup.string().required("required"),
    cons_5: Yup.string().required("required"),
    cons_6: Yup.string().required("required"),
    cons_7: Yup.string().required("required"),
    cons_8: Yup.string().required("required"),
    cons_9: Yup.string().required("required"),
    cons_10: Yup.string().required("required"),
    cons_11: Yup.string().required("required"),
    cons_12: Yup.string().required("required"),
    cons_13: Yup.string().required("required"),
    cons_14: Yup.string().required("required"),
    cons_15: Yup.string().required("required"),
    cons_16: Yup.string().required("required"),
    cons_17: Yup.string().required("required"),
  });
  // submit form
  const onSubmit = async (values, { resetForm }) => {
    await EDU_AXIOS.post(
      process.env.REACT_APP_API_PATH + "/ttm/initialize-general-constraints",
      {
        school_id: school_id,
        cons_1: values.cons_1,
        cons_2: values.cons_2,
        cons_3: values.cons_3,
        cons_4: values.cons_4,
        cons_5: values.cons_5,
        cons_6: values.cons_6,
        cons_7: values.cons_7,
        cons_8: values.cons_8,
        cons_9: values.cons_9,
        cons_10: values.cons_10,
        cons_11: values.cons_11,
        cons_12: values.cons_12,
        cons_13: values.cons_13,
        cons_14: values.cons_14,
        cons_15: values.cons_15,
        cons_16: values.cons_16,
        cons_17: values.cons_17,
      }
    )
      .then(async (res) => {
        resetForm({ values: "" });
        window.location.reload();
        // history.push(`/time-table-management/step-2/${school_id}`);
        notify("success", res.data.message);
      })
      .catch((err) => {
        notify("error", err);
      });
  };
  // get constraints detail from server
  useEffect(async () => {
    setDataLoaded(false);
    const res = await getGenConstraints();
    if (res) {
      setDataLoaded(true);
    }
  }, []);

  // get teacher data from database
  const getGenConstraints = async () => {
    try {
      await EDU_AXIOS.get(
        `${process.env.REACT_APP_API_PATH}/ttm/get-general-constraints/${school_id}`
      )
        .then((resp) => {
          console.log(resp.data.data[0]);
          setDbData(resp.data.data[0]);
        })
        .catch((err) => {
          notify("error", err);
        });
      return true;
    } catch {
      return false;
    }
  };
  return (
    <CCardGroup>
      <CCard className="p-2">
        <CCardBody>
          <CRow>
            <CCol>
              <NavBar id={school_id} />
            </CCol>
          </CRow>
          <CRow>
            <CCol md="8" lg="4" xl="8">
              <h5>General Constraints [ Step 2 ]</h5>
            </CCol>
          </CRow>
          <p className="text-muted">Apply constraints</p>
          {dataLoaded ? (
            <Formik
              initialValues={initialValues}
              validationSchema={validationSchema}
              onSubmit={onSubmit}
              enableReinitialize={true}
            >
              {(formik) => (
                <Form className="row g-3">
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      First period of a day should always be assigned to the
                      class teacher
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="radio"
                        name="cons_1"
                        id="cons_1"
                        inline={true}
                        options={opt}
                      />
                    </CCol>
                  </CRow>
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      A teacher should have at least 1/2/3 (pick one) periods
                      free in a week
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="selectwithkey"
                        name="cons_2"
                        id="cons_2"
                        placeholder="Select..."
                        options={[
                          { text: "1", value: "1" },
                          { text: "2", value: "2" },
                          { text: "3", value: "3" },
                          { text: "NA", value: "NA" },
                        ]}
                      />
                    </CCol>
                  </CRow>
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      A teacher should have at least 1 period free in a day
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="radio"
                        name="cons_3"
                        id="cons_3"
                        inline={true}
                        options={opt}
                      />
                    </CCol>
                  </CRow>
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      Every class should have at least one sports/games period
                      in a week
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="radio"
                        name="cons_4"
                        id="cons_4"
                        inline={true}
                        options={opt}
                      />
                    </CCol>
                  </CRow>
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      Last two period of xxxday will be SUPW/House team meet
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="select"
                        name="cons_5"
                        id="cons_5"
                        placeholder="Select..."
                        options={days}
                      />
                    </CCol>
                  </CRow>
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      Two or more classes can have games period at the same time
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="radio"
                        name="cons_6"
                        id="cons_6"
                        inline={true}
                        options={opt}
                      />
                    </CCol>
                  </CRow>
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      Library to be assigned to one class at a time
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="radio"
                        name="cons_7"
                        id="cons_7"
                        inline={true}
                        options={opt}
                      />
                    </CCol>
                  </CRow>
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      Science Lab/practical period should always be assigned as
                      double period i.e. two consecutive periods together
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="radio"
                        name="cons_8"
                        id="cons_8"
                        inline={true}
                        options={opt}
                      />
                    </CCol>
                  </CRow>
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      Computer Lab period should always be assigned as double
                      period i.e. two consecutive periods together
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="radio"
                        name="cons_9"
                        id="cons_9"
                        inline={true}
                        options={opt}
                      />
                    </CCol>
                  </CRow>
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      Any double period can not have lunch break between them.
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="radio"
                        name="cons_10"
                        id="cons_10"
                        inline={true}
                        options={opt}
                      />
                    </CCol>
                  </CRow>
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      Any double period can not have lunch break between them
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="radio"
                        name="cons_11"
                        id="cons_11"
                        inline={true}
                        options={opt}
                      />
                    </CCol>
                  </CRow>
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      Science lab period when assigned as double period can not
                      have lunch break between them.
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="radio"
                        name="cons_12"
                        id="cons_12"
                        inline={true}
                        options={opt}
                      />
                    </CCol>
                  </CRow>
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      Science lab period when assigned as double period can not
                      have day break between them.
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="radio"
                        name="cons_13"
                        id="cons_13"
                        inline={true}
                        options={opt}
                      />
                    </CCol>
                  </CRow>
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      Computer lab period when assigned as double period can not
                      have lunch break between them
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="radio"
                        name="cons_14"
                        id="cons_14"
                        inline={true}
                        options={opt}
                      />
                    </CCol>
                  </CRow>
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      Computer lab period when assigned as double period can not
                      have day break between them.
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="radio"
                        name="cons_15"
                        id="cons_15"
                        inline={true}
                        options={opt}
                      />
                    </CCol>
                  </CRow>
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      Every class should have at least one sports/games period
                      in a week
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="radio"
                        name="cons_16"
                        id="cons_16"
                        inline={true}
                        options={opt}
                      />
                    </CCol>
                  </CRow>
                  <CRow className="mb-3">
                    <CFormLabel
                      htmlFor="session"
                      className="col-md-8 col-form-label required-field"
                    >
                      Science Lab/practical room should be assigned to one class
                      at a time.
                    </CFormLabel>

                    <CCol md={4}>
                      <EVFormikControl
                        control="radio"
                        name="cons_17"
                        id="cons_17"
                        inline={true}
                        options={opt}
                      />
                    </CCol>
                  </CRow>
                  <CCardFooter>
                    <CButton block color="light" onClick={openModalBox}>
                      <FontAwesomeIcon icon={faTimesCircle} /> Cancel
                    </CButton>
                    <CButton
                      block
                      color="dark"
                      type="submit"
                      disabled={
                        formik.isSubmitting || !formik.dirty || !formik.isValid
                      }
                      style={{ marginLeft: "15px" }}
                    >
                      {formik.isSubmitting ? (
                        <Loader />
                      ) : (
                        <>
                          <FontAwesomeIcon icon={faSave} /> Save
                        </>
                      )}
                    </CButton>
                  </CCardFooter>
                  <CModal visible={visible} onDismiss={closeModalBox}>
                    <CModalHeader onDismiss={closeModalBox}>
                      <CModalTitle>
                        <h5>Cancel</h5>
                      </CModalTitle>
                    </CModalHeader>

                    <CModalBody>Do You Want To Cancel ?</CModalBody>
                    <CModalFooter>
                      <CButton color="light" onClick={closeModalBox}>
                        <FontAwesomeIcon icon={faTimesCircle} /> No
                      </CButton>
                      <Link to="/">
                        <CButton color="dark">
                          <FontAwesomeIcon icon={faCheckCircle} /> Yes
                        </CButton>
                      </Link>
                    </CModalFooter>
                  </CModal>
                </Form>
              )}
            </Formik>
          ) : (
            <Loader />
          )}
        </CCardBody>
      </CCard>
    </CCardGroup>
  );
};

export default general_constraints;
